<template>
    <div class="m-geo">
       <div class="position">
           <i class="el-icon-location" />
           {{$store.state.province}}
           <router-link class="changeCity" to="/changeCity" >切换城市</router-link>
           [
             <a href="" v-for="(item) in nearCity" :key="item.id">{{item.name}}</a>
           ]
       </div>
       <div class="m-user" v-if="!logined">
            <router-link class="login" to="/blank/login" >立即登陆</router-link>
            <router-link class="register" to="/blank/register" >注册</router-link> 
       </div>
        <div class="m-user" v-if="logined">
            <a href="#">已登陆</a>
       </div>
    </div>







</template>
<script>
import {mapState}  from 'vuex'
import Api from "@/api/index.js";
const api = new Api();
export default {
    data(){
        return{
           nearCity:[],
        }
    },
    computed:{
      ...mapState(['logined'])
    },
created(){
    api.getPosition().then((res)=>{
         this.$store.state.province = res.data.data.name;
         this.nearCity = res.data.data.nearCity 
    })

    
}
}
</script>
<style>

</style>



