import Vue from 'vue'
import Vuex from 'vuex'


Vue.use(Vuex);


export default new Vuex.Store({
    state:{
        province:'',
        logined:''
    },
    getters:{
      
    },
    mutations:{
        changeProv(state,payload){
            state.province = payload
        },
        haveLogin(state,payload){
            state.logined = payload
        }
    },
    actions:{
        changeProv({commit},payload){
            commit('changeProv',payload)
        },
        haveLogin({commit},payload){
            commit('haveLogin',payload)
        }
    }
})


