<template>
  <div class="search-panel">
    <el-row class="m-header-searchbar">
      <!-- 美团logo -->
      <el-col :span="3" class="left">
        <img src="https://s0.meituan.net/bs/fe-web-meituan/e5eeaef/img/logo.png" alt>
      </el-col>
      <!-- 搜索框 -->
      <el-col :span="10" class="center">
        <div class="wrapper">
          <el-input v-model="searchWrod" placeholder="请输入内容" @focus="onFocus" @blur="onBlur" @input="input"></el-input>
          <el-button type="primary" icon="el-icon-search"></el-button>

          <dl class="hotPlace" v-show="isFoucsing">
            <dt>热门搜索</dt>
            <dd v-for="(item,index) in hotWord" :key="index">
              <router-link :to="{name:'goodsList',params:{id:item}}">{{item}}</router-link>
            </dd>
          </dl>

          <div class="hot2" v-show="isInput">
            <dl class="searchList">
              <dd v-for="(item,index) in searchWordhot" :key="index">
                <router-link to="/">{{item}}</router-link>
              </dd>
            </dl>
          </div>
        </div>

        <p class="suggest">
          <router-link
            v-for="(item,index) in hotWord"
            :key="index"
            :to="{name:'goodsList',params:{id:item}}"
          >{{item}}</router-link>
        </p>
      </el-col>
    </el-row>
  </div>
</template>
<script>
import Api from "@/api/index.js";
const api = new Api()
export default {
  created() {
    api.getSearchHotWords().then(res => {
      this.hotWord = res.data.data;
    });
    // axios.get('api/meituan/header/search.json',{
    //   params:{
    //     appkey:'fyt2019_1547904500155'
    //   }
    // }).then((res)=>{
    //     console.log(res)
    // })
  },
  data() {
    return {
      isFoucs: false,
      searchWrod: "",
      hotWord: [],
      searchWordhot: []
    };
  },
  computed: {
    isFoucsing() {
      return this.isFoucs && !this.searchWrod;
    },
    isInput() {
      return this.isFoucs && this.searchWrod;
    }
  },
  methods: {
    onFocus() {
      this.isFoucs = true;
    },
    onBlur() {
      setTimeout(() => {
        this.isFoucs = false;
      }, 200);
    },
    input(){
      let value = this.searchWrod;
      api.getSearchWords().then((res)=>{
             this.searchWordhot = res.data.data.list.filter((item,index)=>{
                    return item.indexOf(value) > -1
             })
      })
    }
  }
};
</script>
<style lang="scss">
@import "@/assets/css/public/header/index.scss";
</style>



